            <div id="model-price">{{model.price}}</div>
            <button id="model-buy" type="button">Buy</button>
        </div>
    </div>
</template>

<script>

export default {
    props:{
        model:{
            type:Object,
            required:true,
        }
    }
}
</script>

<style>

img {
    max-width: 450px;
}
#model-title{
    
    grid-area: title;
    font-family: serif;
    color: white;
    text-shadow: 0 0 2px #e19a86, 0 0 1.5px #854535, 0 0 1.5px #5c150c;
    transform: scale(1, 1.5);
    line-height: 1.2em;
