    width:100%;
    height: 18px;
    background:#000080;
}

#model-image{
    grid-area: image;
}

#model-description{
    grid-area: description;
    font-family: serif;
    transform: scale(1, 1.3);
    font-size: 120%;
    line-height: 1em;
    text-align: left;
    color: black;
}

#model-blueprint{
    grid-area: blueprint;

    font-family: sans-serif;
    font-size: 80%;
    transform: scale(1, 1.5);
