<template>
    <div id="visitors">
        <div id="visitors-label">
            Visitors:
        </div>
        <div id="visitors-counter">
            {{value}}
        </div>
    </div>
</template>

<script>
export default {
    data: function(){
        return{value:7076};
    },
    mounted:function() {
        setInterval(()=>{this.value+=Math.floor((Math.random() * 10) + 1);}, 1000);
    }    
}
</script>

<style>
    #visitors{
        color: aquamarine;
        background-color: black;
        width: 100px;
        height: 20px;
        line-height: 20px;
        padding: 15px;
        border: 2px aquamarine;
        text-align: center;
        vertical-align: middle;
        margin: 10px auto 10px auto;
    }
</style>