<template>
    <div id="models-wrapper">
        <div id="models">
            <Model v-for="model in models" v-bind:model="model" :key="model.id"></Model>
        </div>
    </div>
</template>

<script>
import Model from './Model.vue';

var jsonData=require("../assets/modelsData.json");

var modelId=0;
jsonData["models"]=jsonData["models"].reverse();
jsonData["models"].forEach(element => {
    element.id=modelId++;
    element.imgSrc=require('../assets/images/'+element.imgSrc);
});

export default {
    components:{Model},
    data(){
        return {models:jsonData["models"]};
    }
}
</script>

<style>

#models{
    columns: 1;
    column-gap: 20px;
}

#models-wrapper{
    padding: 1.5em;
    max-width: 1000px;
    margin-right: auto;
    margin-left: auto;
}

@media only screen and (max-width: 1023px) and (min-width: 768px) {  
    .models {
    columns: 2;
  }
}
@media only screen and (min-width: 1024px) {
  .models {
    columns: 2;
  }
}
</style>